.projects {
	margin: 0rem auto;
	display: flex;
	width: 80%;
	align-items: center;
	justify-content: space-evenly;
	/* background-color: aqua; */
	font-family: "Barlow Condensed";
	/* background-color: aquamarine; */
}

/* .projectImg {
	background-color: aqua;
} */
.projectImg img {
	/* max-width: 30rem;	 */
	height: 30rem;
	/* animation: renderFromLeft 0.5s ease forwards; */
}

.projectHeader {
	width: 40%;
	text-align: center;
	font-weight: 600;
	/* animation: renderFromRight 0.5s ease forwards; */
}

.projectHeader h1 {
	font-size: 3rem;
}

.projectHeader div {
	margin-top: 2rem;
	font-size: 1.8rem;
}

.projectHeading {
	font-size: 3rem;
	text-align: center;
}

.projectList {
	display: flex;
	flex-wrap: wrap;
	/* background-color: aquamarine; */
	gap: 1rem;
	justify-content: center;
	margin: 2rem auto;
	width: 90%;
}

.moreProject {
	display: flex;
	justify-content: center;
}

@media (max-width: 768px) {
	.projects {
		margin: 0rem auto;
		flex-direction: column;
		width: 90%;
	}

	.projectImg img {
		margin-top: 2rem;
		height: 15rem;
	}

	.projectHeader {
		width: 100%;
	}

	.projectHeader div {
		margin-top: 1rem;
		font-size: 1.5rem;
	}
}

@media (max-width:768px) {
	.app-content {
		margin-top: 4rem;
	}

}

@keyframes renderFromLeft {
	0% {
		opacity: 0;
		transform: translateX(-100%);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes renderFromRight {
	0% {
		opacity: 0;
		transform: translateX(100%);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

.moreProjectBtn {
	padding: 0.4em 1.4em;
	border: none;
	outline: none;
	color: rgb(255, 255, 255);
	background: #111;
	cursor: pointer;
	position: relative;
	z-index: 0;
	border-radius: 10px;
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
}

.moreProjectBtn:before {
	content: "";
	background: linear-gradient(45deg,
			#ff0000,
			#ff7300,
			#fffb00,
			#48ff00,
			#00ffd5,
			#002bff,
			#7a00ff,
			#ff00c8,
			#ff0000);
	position: absolute;
	top: -2px;
	left: -2px;
	background-size: 400%;
	z-index: -1;
	filter: blur(5px);
	-webkit-filter: blur(5px);
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	animation: glowing-button-85 20s linear infinite;
	transition: opacity 0.3s ease-in-out;
	border-radius: 10px;
}

@keyframes glowing-button-85 {
	0% {
		background-position: 0 0;
	}

	50% {
		background-position: 400% 0;
	}

	100% {
		background-position: 0 0;
	}
}

.moreProjectBtn:after {
	z-index: -1;
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	background: #222;
	left: 0;
	top: 0;
	border-radius: 10px;
}