.educationHeader {
    display: flex;
    gap: 5rem;
    width: 80%;
    margin: 0rem auto;
    font-family: "Barlow Condensed";
}

.eduImg {
    margin: auto;
    height: 40rem;
    width: 30rem;
    /* animation: renderFromLeft 0.5s ease forwards; */
}

.eduImg img {
    height: 30rem;
    object-fit: cover;
}

.educationCard {
    margin-top: 1.5rem, auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    /* background-color: aqua; */
    /* animation: renderFromRight 0.5s ease forwards; */
}

.educationCard h1 {
    font-size: 4rem;
    font-weight: 700;
}

.educationCard h2 {
    font-size: 3rem;
    font-weight: 700;
}

.codingInfo {
    margin-top: 1.5rem;
    width: 100%;
    /* background-color: aquamarine; */
}

.progressBar {
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    /* background-color: antiquewhite; */
    width: 100%;
}

.progressBar label {
    text-align: center;
    width: 20%;
    font-size: 1.4rem;
    font-weight: 800;
}

.progressBar h5 {
    text-align: center;
    width: 15%;
    font-size: 1.5rem;
    font-weight: 800;
}

.progressBar progress {
    width: 80%;
    -webkit-appearance: none;
    appearance: none;
    height: 2rem;
}

.progressBar progress::-webkit-progress-bar {
    background-color: rgb(178, 129, 178);
    border-radius: 0.5rem;
}

.progressBar progress::-webkit-progress-value {
    animation: load 4s normal forwards;
    border-radius: 0.5rem;
    background-color: purple;
}

@keyframes load {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@media (max-width:768px) {
    .education {
        padding-top: 0rem;
    }

    .educationHeader {
        flex-direction: column;
        width: 90%;
    }

    .eduImg {
        height: 15rem;
        width: 20rem;
    }

    .eduImg img {
        height: 20rem;
        width: 20rem;
        object-fit: contain;
    }

    .educationCard {
        margin-top: -2rem;
        width: 100%;
    }

    .educationCard h1 {
        /* text-align: center; */
        font-size: 4rem;
        font-weight: 700;
    }

    .educationCard h2 {
        /* margin-top: 1.5rem; */
        /* text-align: center; */
        font-size: 1.5rem;
        font-weight: 700;
    }

    .progressBar label {
        /* text-align: center; */
        width: 30%;
        font-size: 1.5rem;
    }

    .progressBar progress {
        width: 60%;
        height: 1.5rem;
    }
}

@media (max-width:768px) {
    .app-content {
        margin-top: 4rem;
    }

}

@keyframes renderFromLeft {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes renderFromRight {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}